.bar + .bar > .clef {
    display: none;
}

.bar > .clef {
    justify-self: start;
    grid-column: 1 / span 1;
    margin-left: -0.125em;
    margin-right: 0.25em;
}

.bar > .timesig {
    justify-self: start;
    grid-column: 1 / span 1;
    margin-left: 0;
    margin-right: 0.5em;

    --timesig-gap: 0.5em;
}

.bar > .acci:not([data-beat]) + .timesig {
    /* If they are preceded by a stave, move them right */
    --timesig-gap:   0.3em;
    --clef-acci-gap: 0.9em;
    --acci-acci-gap: 0.3em;
}


/* Sort out ordering of clef / keysig / timesig */

.bar > .clef ~ .acci:not([data-beat]) {
    /* If they are preceded by a stave, move them right */
    --clef-acci-gap: 0.9em;
}
