    --clef-timesig: 1em;
    margin-left: var(--clef-timesig);
}
*/

.bar > .drum-clef + .timesig,
.bar > .percussion-clef + .timesig {
	--clef-timesig: 0.2em;
}


/* Accidentals */

.bar > .acci {
    grid-column-end: span 1;
    justify-self: end;
    margin-left:  var(--acci-margin-left);
    /* Accidentals must have column gap and head margin on their right negated
       before adding the proper margin, becuase they are aligned to the end of
       the column before their head. */
    margin-right: calc(-1 * var(--bar-column-gap) + 1em * var(--acci-margin-right));
    /* Place accidentals in the column before their related heads */
    grid-column-start: calc(var(--column-beat-0) + round(var(--beat) * var(--columns-per-beat)) - 1);
}

.bar > .acci-1 { margin-right: calc(-1 * var(--bar-column-gap) + 1em * var(--acci-margin-right) + 0.27em) }
.bar > .acci-2 { margin-right: calc(-1 * var(--bar-column-gap) + 1em * var(--acci-margin-right) + 0.54em) }
.bar > .acci-3 { margin-right: calc(-1 * var(--bar-column-gap) + 1em * var(--acci-margin-right) + 0.09em) }
.bar > .acci-4 { margin-right: calc(-1 * var(--bar-column-gap) + 1em * var(--acci-margin-right) + 0.36em) }
.bar > .acci-5 { margin-right: calc(-1 * var(--bar-column-gap) + 1em * var(--acci-margin-right) + 0.63em) }

.bar > .acci-cluster        { margin-right: calc(-1 * var(--bar-column-gap) + 1em * var(--acci-margin-right) + 1em * var(--head-width)) }
.bar > .acci-cluster.acci-1 { margin-right: calc(-1 * var(--bar-column-gap) + 1em * var(--acci-margin-right) + 0.27em + 1em * var(--head-width)) }
.bar > .acci-cluster.acci-2 { margin-right: calc(-1 * var(--bar-column-gap) + 1em * var(--acci-margin-right) + 0.54em + 1em * var(--head-width)) }
.bar > .acci-cluster.acci-3 { margin-right: calc(-1 * var(--bar-column-gap) + 1em * var(--acci-margin-right) + 0.09em + 1em * var(--head-width)) }
.bar > .acci-cluster.acci-4 { margin-right: calc(-1 * var(--bar-column-gap) + 1em * var(--acci-margin-right) + 0.36em + 1em * var(--head-width)) }
.bar > .acci-cluster.acci-5 { margin-right: calc(-1 * var(--bar-column-gap) + 1em * var(--acci-margin-right) + 0.63em + 1em * var(--head-width)) }


/* Notes and ledges */

.bar > .note,
.bar > .ledge {
	grid-column-end: span 1;
	justify-self: start;
}

.bar > .cluster-note.up-note {
    margin-left: calc(1em * var(--head-width));
}

.bar > .cluster-note.up-note > .head {
    grid-column: 2;
    margin-left: calc(-0.6em * var(--stem-width));
}

/* ------------------------------------------------ Too hacky --------------- */
.bar > .cluster-note.down-note {
    justify-self: end;
    /* Push cluster notes on down stems into preceding column */
    /* TODO: This is going to cause a problem with accidentals!! It doesnt really
       work anyway, its all a bit of a hacky clusterfuck */
    grid-column-start: calc(var(--column-beat-0) + round(var(--beat) * var(--columns-per-beat)) - 1);
    margin-right: calc(-1em * var(--stem-width) + -1 * var(--bar-column-gap) - 0.003em);
}

.bar > .cluster-note.down-note::before {
    /* Move flag back into position */
    transform: translate(calc(1em * var(--head-width)), calc(var(--stem-height) * 1em));
}
/* -------------------------------------------------------------------------- */

.bar > .ledge {
    width: 0.625em;
	/* Make ledger take up same space as head */
	margin-left:  calc(-0.5 * (0.625em - 1em * var(--head-width)));
	margin-right: calc(-0.5 * (0.625em - 1em * var(--head-width)));
}


/* Rests
Short rests inhabit one column, longer rests are centered
in their duration span. */

.bar > .rest {
	justify-self: start;
    grid-column-end: span 1;
}

/* Anything shorter than a quarter rest */
.bar > .rest[data-duration^="."],
.bar > .rest[data-duration^="0."] {
    margin-left:  0.03125em;
    margin-right: 0.03125em;
}

/* Triplet quarter rest */
.bar > .rest[data-duration="0.66"],
/* A quarter rest */
.bar > .rest[data-duration="1"],
/* A dotted quarter rest */
.bar > .rest[data-duration^="1.5"],
/* A double dotted quarter rest */
.bar > .rest[data-duration^="1.75"] {
	margin-left:  0.0625em;
    margin-right: 0.0625em;
}

.bar > .rest[data-duration^="1.33"] {
    justify-self: center;
    margin-left: 0.5em;
    margin-right: 0.5em;
    /* Make 2 beat rest span its durations */
    grid-column-end: span calc(round(var(--columns-per-beat) * var(--duration)) - 1);
}

.bar > .rest[data-duration="2"],
.bar > .rest[data-duration="3"],
.bar > .rest[data-duration="3.5"],
.bar > .rest[data-duration="4"],
.bar > .rest[data-duration="6"],
.bar > .rest[data-duration="7"],
.bar > .rest[data-duration="8"] {
	justify-self: center;
	margin-left: 0.75em;
	margin-right: 0.75em;
    /* Make 2 beat rests and longer span their durations */
    grid-column-end: span calc(round(var(--columns-per-beat) * var(--duration)) - 1);
}

.bar > .rest[data-duration="1"] + .rest[data-duration="2"] {
    margin-left: 0.5em;
}


/* Tuplets */

.bar > .tuplet {
    justify-self: stretch;
    align-self: center;
    /* Make tuplets span their heads and rests and no more */
    grid-column-end: span calc(1 + round(var(--duration) * var(--columns-per-beat) * (var(--divisor) - 1) / var(--divisor)));
}

.bar > .tuplet[data-duration="4"],
.bar > .tuplet[data-duration="8"] {
    /* Account for minim and whole rests, symbols that span multiple columns */
    grid-column-end: span calc(round(var(--duration) * var(--columns-per-beat) - 1));
}


/* Beams */

.bar > .beam {
	justify-self: stretch;
	width: auto;
	max-width: none;
    /* Beams span from first head to last head. Their duration is set to the
       difference between the two (not to the total duration) */
    grid-column-end: span calc(round(nearest, var(--duration) * var(--columns-per-beat)));
}

.bar > .up-beam {
	margin-left: calc(-1em * var(--stem-width));
	margin-right: calc(-1 * var(--bar-column-gap));
	transform: translate(calc(1em * var(--head-width)), calc(1em * var(--translate-y, 0)));
}

.bar > .down-beam {
	margin-left:  0;
	margin-right: calc(-1em * var(--stem-width) - var(--bar-column-gap));
	transform: translate(0, calc(1em * var(--translate-y, 0)));
}

/* Words */

.bar > .chord {
	justify-self: start;
	margin-left:  0;
	margin-right: 0.25em;
    /* Make chords span their full duration */
    grid-column-end: span calc(round(nearest, var(--duration) * var(--columns-per-beat)));
}

.bar > .lyric {
	justify-self: start;
	margin-left:  0;
	margin-right: 0.25em;
    grid-column-end: span calc(round(nearest, var(--duration) * var(--columns-per-beat)));
}

.bar > .tie {
	justify-self: stretch;
	/* Make tie span from end of one head to start of next. */
	margin-left: calc(1.125em * var(--head-width));
	min-width: 0.5em;
    /* Ties span from one head to another minus the accidentals column */
    grid-column-end: span calc(round(var(--duration) * var(--columns-per-beat)) - 1);
}

