/* Double bar line */

.bar > .barline {
    grid-column: -1;
    white-space: nowrap;
}

.bar {
    position: relative;
}

.bar > .barcount {
    position: absolute;
    grid-column: auto;
    grid-row: auto;
    left: -1em;
    top: calc(50% + 2.4em);
    width: 2em;
    font-family: system-ui;
    font-size: 0.3125em;
    line-height: 0.8em;
    text-align: center;
    white-space: nowrap;
}

.bar > .barrepeat {
    justify-self: center;
    margin-left: 0.5em;
    margin-right: 0.5em;
    grid-column-end: span calc(var(--columns-per-beat) * var(--duration));
}

.end-bar + .bar::before {
    /* Bar count */
    content: attr(data-count);
    position: absolute;
    grid-column: 1;
    grid-row: barcount;
    left: calc(-1 * var(--bar-padding-left) / 0.3125 - 1em);/* - 1em / 0.3125);*/
    top: 0;
    width: 2em;
    font-family: system-ui;
    font-size: 0.3125em;
    line-height: 0.8em;
    text-align: center;
    white-space: nowrap;
}

.bar:first-child::before,
:not(.bar) + .bar::before {
    display: none;
}
