.stave::before {
    /* Clef */
    align-self: center;
}

.stave > svg {
    grid-column: 1 / span -1;
    grid-row: stave-top / stave-bottom;
    overflow: visible;
}

.stave > .clef {
    align-self: center;
}

.stave > .treble-clef {
    grid-row: G4;
}

.stave > .treble-up-clef {
    grid-row: G5;
}

.stave > .treble-down-clef {
    grid-row: G3;
}

.stave > .bass-clef {
    grid-row: F3;
}

.stave > .alto-clef {
    grid-row: C4;
}

.stave > .drum-clef,
.stave > .percussion-clef {
    grid-row: stave-center;
}

.stave > .timesig {
    grid-row: stave-center;
    align-self: center;
}

.stave > .note {
    align-self: center;
}

.stave > .chord {
    grid-row: chords;
    align-self: center;
    margin-top: 0;
    margin-bottom: 0;
}

.stave > .acci {
    grid-row-end: span 1;
    align-self: center;
}

.stave > .ledge {
    /* Stretch ledge from one named row to another plus 1 */
    align-self: stretch;
    height: auto;
    overflow: hidden;
}

.stave > .up-ledge[data-pitch]   {
    grid-row-end: stave-top;
    margin-bottom: 0.125em;
}

.stave > .down-ledge[data-pitch] {
    grid-row-start: stave-bottom;
    position: relative;
    top: 0.125em;
}

/*
.stave > .stem {
    align-self: center;
}
*/
.stave > .up-stem {
    align-self: end;
    margin-bottom: 0.0625em;
}

.stave > .down-stem {
    align-self: start;
    margin-top: 0.0625em;
}

/*.stave > .tail {
    align-self: center;
}*/

.stave > .beam {
    align-self: stretch;
    width: auto;
}

.stave > .up-beam {
    position: relative;
    top: calc(-0.9375em * var(--stem-height));
}

.stave > .down-beam {
    position: relative;
    top: calc(1em * var(--stem-height));
}

.stave > .rest {
    grid-row: stave-center;
    align-self: center;
}

.stave > .rest[data-duration="4"],
.stave > .rest[data-duration="6"] {
    grid-row: 13;
}

.stave > .tie,
.stave > .down-tie {
    align-self: start;
    margin-top: 0.225em;
}

.stave > .up-tie {
    align-self: end;
    margin-bottom: 0.225em;
}

.stave > .lyric {
    grid-row: lyrics;
    align-self: end;
}

.stave > .barrepeat {
    align-self: center;
    grid-row: stave-center;
}
